import Vue from 'vue'
import VueRouter from 'vue-router' // 导入路由
import Films from '@/views/Films.vue'
import Nowplaying from '@/views/films/Nowplaying.vue'
import Comingshow from '@/views/films/Comingshow.vue'
import Center from '@/views/Center'
// import Cinemas from '@/views/Cinemas.vue'
import Search from '@/views/Search.vue'
import City from '@/views/City.vue'
import Detail from '@/views/Detail.vue'
import Login from '@/views/Login.vue'

// 挂载根实例,就能以 this.$router 的形式访问它，并且以 this.$route 的形式访问当前路由
// 即注册路由插件 , 两个全局 router-view router-link
Vue.use(VueRouter)


// 定义一些路由,配置表  
// 每个路由都需要映射到一个组件。
const routes = [
  // 还有平级关系 的二级路由,不能写在children数组中
  {
    path: '/cinemas/search',
    component: Search
  },

  {
    path: '/films',
    component: Films,
    // 嵌套路由, 嵌套的二级路由 
    children: [
      {
        path: '/films/nowplaying',
        component: Nowplaying
      },
      {
        path: '/films/comingshow',
        component: Comingshow
      },
      // 二级路由的重定向
      {
        path: '/films',
        redirect: '/films/nowplaying'
      }
    ]
  },
  // 详情页  动态路由 冒号是固定的,myid是自己定义的
  {
    name: 'myownDetail', //命名路由,但是跳转规则有改变 (获取命名路由的名字)
    path: '/detail/:myid', // 动态二级路由,前面是固定的,后面是随意的,即加了冒号后就是随意的
    component: Detail
  },
  { // 一级路由
    path: '/cinemas',
    // component: Cinemas
    // 路由懒加载 需要的时候再导入 动态的import()
    component: () => import('@/views/Cinemas') // 基于底层的webpack代码分割功能
  },
  // 别名 alias属性
  // {
  //   path: '/center',
  //   component: Center
  //   alias: '/mycenter'
  // },
  {
    path: '/center',
    component: Center,
    meta: {
      //可以给需要授权的路由加meta属性,方便拦截,meta也叫路由元信息
      ismyRequired: true // ismyRequired这个属性是自定义的
    },
    // 路由独享守卫,就是在路由内定义beforeEnter
    /* beforeEnter: (to, from, next) => {
      if (localStorage.getItem('token')) {
      next()
    } else {
      // next('/login')
      next({
        path: '/login',
        query: { myredirect: to.fullPath } //redirect自定义的,可以记录是从哪个页面被强制到login的
      })
    }
    } */
  },
  {
    path: '/login',
    component: Login
  },
  {
    path: '/city',
    component: City
  },
  // 重定向
  {
    // path: '/',
    path: '*',
    redirect: '/films'
  }
]

// 创建路由实例并传递 `routes` 配置
const router = new VueRouter({
  mode: 'hash', // 路由模式,histroy模式,去掉前端路由的#号
  // base: process.env.BASE_URL,
  routes
})

/* 路由模式
带#号的路由一定是前端路由,
不带#号浏览器就无法分清是前端路由还是后端路由
因此这个模式浏览器会朝着服务器发请求
如果后端没有配置,就会返回404.而在开发环境下已经配置好了,就没有报错

url匹配不到任何静态资源,则应该返回同一个index.html页面,这个页面就是你app依赖的页面
*/

// 路由的全局拦截(守卫)
router.beforeEach((to, from, next) => {
  // console.log(to, to.fullPath, to.path); //to.fullPath可以获取路径参数
  // to将要访问的 路由信息对象
  //  from 将要离开的路由信息对象
  // next 是一个函数,不拦截就调用 next()

  // 方法一: 
  // if(to.fullPath === '/center'){}
  // 方法二:
  // if(to.meta.ismyRequired) {}

  if (to.meta.ismyRequired) {
    // 判断 本地存储中是否token
    if (localStorage.getItem('token')) {
      next()
    } else {
      // next('/login')
      next({
        path: '/login',
        query: { myredirect: to.fullPath } //redirect自定义的,可以记录是从哪个页面被强制到login的
      })
    }
  } else {
    next()
  }
})

export default router
