<template>
  <div>
    <span @click="handleback"> &lt; </span>
    <slot></slot>
  </div>
</template>
<script>
export default {
  methods: {
    handleback() {
      // history.back() 一样
      this.$router.back() // 返回上一个页面
    }
  }
}
</script>

