  height: 40px;
  line-height: 40px;
  text-align: center;
  background: #fff;
  font-size: 18px;
}

span {
  position: fixed;
  left: 10px;
  top: 0;
  font-size: 18px;
  height: 40px;
  line-height: 40px;
}
</style>