import Vue from 'vue'
import Vuex from 'vuex'
import http from '@/util/http'
import createPersistedstate from 'vuex-persistedstate'
Vue.use(Vuex)

// this.$store === store ,通过this.$store能访问到整个store对象
export default new Vuex.Store({
  // vuex 持久化 
  plugins: [createPersistedstate({
    // storage: window.sessionStorage, //默认是localStorage
    reducer(state) {
      // 可选择只存储的内容
      return {
        cityId: state.cityId,
        cityName: state.cityName
      }
    }
  })],

  // state是固定的,就是公共状态,任何组件都能随意访问他
  state: {
    cityId: '310100',
    cityName: '上海',
    cinemaList: [],
    tabbarShow: true
  },
  getters: {
  },

  // mutations统一管理修改状态的地方,修改过程会被devtools(开发工具) 给记录了状态的修改
  // mutations只支持同步代码
  mutations: {
    // 第一个参数是state,第二个参数就是传来的参数值
    changeCityName(state, cityName) { // 同步工作流
      // console.log(cityName);
      state.cityName = cityName // 修改状态
    },

    changeCityId(state, cityId) {
      state.cityId = cityId
    },

    changeCinemasData(state, data) {
      state.cinemaList = data
    },
    clearCinemaList(state) {
      state.cinemaList = []
    },
    changeTabbarHide(state) {
      state.tabbarShow = false
    },
    changeTabbarShow(state) {
      state.tabbarShow = true
    }
  },

  // actions类似于mutations 但是支持同步和异步操作
  actions: {
    // 接收的第一个参数是store对象,第二个是传来的参数
    getCinemasData(store, cityId) {
      // 返回的是promise对象,可以return出去,使得分发的时候可以调用.then方法
      return http({
        url: `/gateway?cityId=${cityId}&ticketFlag=1&k=5295042`,
        headers: {
          'X-Host': 'mall.film-ticket.cinema.list'
        }
      }).then(res => {
        // console.log(res.data.data);
        // 异步请求接收数据后还需要传给mutation修改状态（即缓存数据到vuex中，使得下一次不用重复请求数据）
        store.commit('changeCinemasData', res.data.data.cinemas)
      })
    }
  },
  modules: {
  }
})

/* 
vuex 管理保存公共状态 (分散在各个组件内的状态,统一管理)
但是一刷新页面就回到初始化,vuex默认是管理在内存,所以一刷新,公共状态就丢失

vuex 支持 持久化 
下载vuex-persistedstate



vuex项目应用
1.非父子通信 (同步)
2.后端数据的缓存快照,减少重复请求数据,减轻服务器压力,提高用户体验 (异步问题)
 actions使用步骤 
    1.通过this.$store.dispatch分发actions函数,并传参数.
    2.在vuex中,在actions中定义该函数,接收参数,发起异步请求
    3.发起异步请求后获得的数据,通过store.commit传给mutations进行缓存
    4.在mutations中定义函数接收数据


vuex的简写方法
mapState mapActions mapMutations
*/
