<template>
  <div>
    <div class="swiper">
      <div class="swiper-wrapper">
        <slot></slot>
      </div>
      <div class="swiper-pagination"></div>
    </div>
  </div>
</template>

<script>
// 根据swiper英文文档规则导入
import Swiper from 'swiper/bundle' // 导入js
import 'swiper/css/bundle' // 导入样式
export default {
  props: {
    loop: {
      type: Boolean,
      default: true,
    },
  },
  mounted() {
    // 异步请求就会使Swiper初始化过早
    // 解决方法:给swiper组件绑定一个key值
    new Swiper('.swiper', {
      pagination: {
        el: '.swiper-pagination',
      },
      loop: this.loop,
      autoplay: {
        delay: 2500,
        disableOnInteraction: false
      }
    })
  },
}
</script>