<template>
  <div>
    <!-- swiper轮播 还需要导入样式-->
    <!-- 解决初始化过早 -->
    <flim-swiper :key="list.length">
      <flim-swiper-item v-for="item in list" :key="item.filmId" class="flimItem">
        <img :src="item.poster" alt="">
      </flim-swiper-item>
    </flim-swiper>
    <!-- 单页面的组件一般放在views文件夹中,可复用的组件一般放在components文件夹中 -->
    <!-- <h1>Film--一级路由</h1> -->

    <!-- <div>二级的声明式导航将在以下切换</div> -->
    <hr>
    <film-tab></film-tab>

    <!-- 插入的位置 -->
    <router-view></router-view>
  </div>
</template>

<script>
import flimSwiper from '@/components/flims/FlimSwiper.vue'
import flimSwiperItem from '@/components/flims/FilmSwiperItem.vue'
import filmTab from '@/components/flims/FilmTab.vue'
import http from '@/util/http'
export default {
  data() {
    return {
      list: []
    }
  },
  created() {
    // fetch('/maoyan.json').then(res => res.json()).then(res => {
    //   console.log(res.data);
    //   this.list = res.data
    // })
    http({
      url: `/gateway?cityId=310100&pageNum=1&pageSize=7&type=1&k=5196770`,
      headers: {
        'X-Host': 'mall.film-ticket.film.list'
      }
    }).then(res => {
      this.list = res.data.data.films
    })
  },
  components: {
    flimSwiper,
    flimSwiperItem,
    filmTab
  }
}

/* 有些请求需要二次校验,即模拟header或cookie
 比如卖座电影的接口https://m.maizuo.com/gateway?cityId=440100&pageNum=1&pageSize=10&type=1&k=2976621
  可利用的小工具 postman
  添加X-client-Info
  和X-Host
*/
</script>

<style lang="scss" scoped>
.flimItem {
  width: 200px;


  img {
    width: 100%;
    height: 400px;
  }
}
</style>
