<template>
  <div>
    <van-nav-bar title="定位城市" left-text="返回" left-arrow @click-left="onClickLeft" />
    <van-index-bar :index-list="computeList" @select="handleSelect" :sticky-offset-top="0">
      <div v-for="data in listCities" :key="data.type">
        <van-index-anchor :index="data.type" />
        <van-cell :title="item.name" v-for="item in data.list" :key="item.cityId" @click="handleClick(item)" />
      </div>
    </van-index-bar>
  </div>
</template>

<script>
import http from '@/util/http'
import { Toast } from 'vant'
// import { mapMutations } from 'vuex'
import obj from '@/util/mixinObj'
// 混入使用 例子
/* 混入作用: 就是能混入一些公共的内容,只要做成 公共对象 即可,谁用就使用mixins导入即可
优势: 项目中有相同函数就不会留下,相同methods时不同的方法名也会留下
var obj = {
  // 1.自己定义一个对象
  mycreated() {
    console.log('创建完成-不是生命周期函数,混入项目中不调用也不会自动执行');
  },
  // 在混入前是自定义函数,混入后变成生命周期函数
  mounted() {
    this.changeTabbarHide()
  },
  created() {
    this.changeTabbarHide()
  },
  destroyed() {
    this.changeTabbarShow()
  },
} */

export default {
  // 2.将自定义的对象混到项目中
  mixins: [obj], //mixins是固定写法
  data() {
    return {
      listCities: [
        // 以二维数组的形式
        // {
        //   type: 'A',
        //   list: ['A1', 'A2']
        // },
        // {
        //   type: 'B',
        //   list: ['B1', 'B2']
        // },
      ]
    }
  },
  computed: {
    computeList() {
      return this.listCities.map(item => item.type)
    }
  },
  created() {

    http({
      url: `/gateway?k=2513634`,
      headers: {
        'X-Host': 'mall.film-ticket.city.list'
      }
    }).then(res => {
      // console.log(res.data.data.cities);

      // 1.将获取的后端数据进行分组
      // 2.利用转换后的数组,结合组件库渲染页面
      this.listCities = this.renderCity(res.data.data.cities)
    })
  },
  mounted() {
    // this.changeTabbarHide()在每个组件重复调用比较繁琐,解决方法:使用混入mixin
    // 混入即将一个对象与另一个组件对象混在一起,一个混入对象可以包含任意组件选项.
    // this.changeTabbarHide()
  },
  // destroyed() {
  //   this.changeTabbarShow()
  // },
  methods: {
    // ...mapMutations(['changeTabbarHide', 'changeTabbarShow']),

    handleSelect(data) {
      // console.log(data);
      Toast(data)
    },
    handleClick(item) {
      // console.log(item);

      // 通过id获取对应的数据,传统的跳转方法(传统的多页面方案)
      // location.href = '#/cinemas?cityname='+item.name
      // 方法二: cookie localStorage


      //单页面方案
      // 1.中间人模式
      // 2.bus事件总线 $on , $emit

      // vuex---状态管理模式 this.$store
      // console.log(this.$store.state);

      // 错误用法,直接在组件内修改状态.
      // this.$store.state.cityName = item.name
      // this.$router.back()

      // 正确用法,将要修改的状态, 通过自己定义的方法(changeCityName)和参数一起提交给mutations,mutations就会接收到传来的参数
      this.$store.commit('changeCityName', item.name) // commit是固定的
      this.$store.commit('changeCityId', item.cityId) // commit是固定的
      this.$router.back()
    },

    onClickLeft() {
      this.$router.back()
    },
    renderCity(list) {
      // console.log(list);
      let letterList = []
      let citylist = []
      for (let i = 65; i < 91; i++) {
        // String.fromCharCode(i) 可以将ASCII转换成字母
        letterList.push(String.fromCharCode(i))
      }

      letterList.forEach(letter => {
        let newList = list.filter(item => item.pinyin.substring(0, 1).toUpperCase() === letter)

        // console.log(newList); //26个数组,其中包括空数组

        // 如果是空数组就不push,短路
        newList.length > 0 && citylist.push({
          type: letter,
          list: newList
        })
        // console.log(this.listCities);
      })
      return citylist
    }
  },
}
</script>