const obj = {
  // 当组件和混入对象含有同名选项时，这些选项将以恰当的方式进行“合并”。
  mycreated() {
    console.log('创建完成-不是生命周期函数,混入项目中不调用也不会自动执行');
  },
  // 在混入前是自定义函数,混入后变成生命周期函数
  mounted() {
    this.$store.commit('changeTabbarHide')
  },
  created() {
    this.$store.commit('changeTabbarHide')
  },
  destroyed() {
    this.$store.commit('changeTabbarShow')
  },
}

export default obj 