<template>
  <!-- <div style="background: pink;">Search --- 平级关系的嵌套路由</div> -->
  <div>
    <van-search v-model="value" show-action placeholder="请输入搜索关键词" @search="onSearch" @cancel="onCancel" />

    <cinemas-list v-if="value" :cinemaslist="computedList"></cinemas-list>
  </div>
</template>

<script>
import cinemasList from '@/components/cinemas/CinemasList.vue'
import obj from '@/util/mixinObj'
export default {
  mixins: [obj],
  components: {
    cinemasList
  },
  data() {
    return {
      value: ''
    }
  },
  mounted() {
    if (this.$store.state.cinemaList.length === 0) {
      this.$store.dispatch('getCinemasData', this.$store.state.cityId)
    } else {
      console.log('search-缓存');
    }
  },
  methods: {
    onSearch() { },
    onCancel() {
      this.$router.back()
    }
  },
  computed: {
    computedList() {
      return this.$store.state.cinemaList.filter(item => item.name.toUpperCase().includes(this.value.toUpperCase()) || item.address.toUpperCase().includes(this.value.toUpperCase()))
    }
  }
}
</script>