<template>
  <div>
    <!-- <h2>comingshow---二级路由</h2> -->
    <!-- vant实现懒加载 -->
    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad" :immediate-check="false">
      <van-cell v-for="item in list" :key="item.filmId" @click="handleChangePage(item.filmId)">
        <img :src="item.poster" alt="">
        <div class="right">
          <div class="title">{{ item.name }}</div>
          <div class="actor">主演: {{ item.actors | actorFilter }}</div>
          <div class="nation">上映时间 {{ item.premiereAt | dateFilter }} </div>
          <div class="nation">{{ item.nation }} | {{ item.runtime }}分钟</div>
        </div>
      </van-cell>
    </van-list>
  </div>
</template>

<script>
import http from '@/util/http'
import moment from 'moment'
export default {
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      current: 1,
      total: 1 // 后端数据总长度
    }
  },
  methods: {
    handleChangePage(id) {
      this.$router.push({
        name: 'myownDetail',
        params: {
          myid: id // myid是前面定义路由规则时自己定义的
        }
      })
    },
    onLoad() {
      console.log('到底了'); //一开始数据没回来就触发了
      if (this.list.length >= this.total) {
        this.finished = true;
        return false
      }
      this.current++
      http({
        url: `/gateway?cityId=${this.$store.state.cityId}&pageNum=${this.current}&pageSize=10&type=2&k=1925509`,
        headers: {
          'X-Host': 'mall.film-ticket.film.list'
        }
      }).then(res => {
        this.list = [...this.list, ...res.data.data.films]
        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成,总长度匹配,禁用懒加载功能(从详情页返回的时候,因为数据还没回来造成相等,出现bug)
        if (this.list.length === this.total && this.total != 0) {
          this.finished = true;

        }
      })
    },
  },
  created() {
    http({
      url: `/gateway?cityId=${this.$store.state.cityId}&pageNum=${this.current}&pageSize=10&type=2&k=1925509`,
      headers: {
        'X-Host': 'mall.film-ticket.film.list'
      }
    }).then(res => {
      this.list = res.data.data.films
      this.total = res.data.data.total
    })
  },
  filters: {
    actorFilter(actorList) {
      if (actorList === undefined) {
        return '暂无主演'
      }
      return actorList.map(item => item.name).join(', ')
    },
    dateFilter(time) {
      // console.log(moment(time).format('YYYY-MM-DD'));
      return moment(time * 1000).format('YYYY-MM-DD')
    }

  }

}
</script>

<style lang="scss" scoped>
.van-list {
  list-style: none;
  margin: 0;
}

.van-cell {
  overflow: hidden;
  padding: 5px;

  img {
    float: left;
    width: 66px;
  }

  .right {
    margin-left: 70px;

  }

  .title {
    font-size: 16px;
  }


  .nation,
  .actor {
    font-size: 12px;
  }

  .actor {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 200px;

  }
}
</style>