// 方法二,基于axios自带的方法封装

const http = axios.create({
  baseURL: 'https://m.maizuo.com',
  timeout: 5000,
  headers: {
    'X-Client-Info': '{ "a": "3000", "ch": "1002", "v": "5.2.1", "e": "16616703481265765516836865" }',
  }

})
//发请求之前拦截---showLoading
http.interceptors.request.use(function (config) {
  Toast.loading({
    message: '加载中...',
    forbidClick: true,
    duration: 0
  });
  return config
}, function (error) {
  return Promise.reject(error)
})

// 在成功后拦截 ----hideLoading
http.interceptors.response.use(function (response) {
  Toast.clear()
  return response
}, function (error) {
  Toast.clear()
  return Promise.reject(error)
})

export default http