<template>
  <div>
    <div class="swiper" :class="name">
      <div class="swiper-wrapper">
        <slot></slot>
      </div>

    </div>
  </div>
</template>

<script>
// 根据swiper英文文档规则导入
import Swiper from 'swiper/bundle' // 导入js
import 'swiper/css/bundle' // 导入样式
export default {
  props: {
    preview: {
      type: Number,
      default: 3,
      required: true
    },
    name: {
      type: String,
      default: 'swiper',
      required: true
    }
  },
  mounted() {
    // 异步请求就会使Swiper初始化过早
    // 解决方法:给swiper组件绑定一个key值
    new Swiper(`.${this.name}`, {
      slidesPerView: this.preview,
      spaceBetween: 30,
      freeMode: true
    })
  },
}
</script>