import Vue from 'vue' // es6的默认导入方式
import App from './App.vue' // 导入根组件
import router from './router' // 引入路由
import store from './store'
import 'element-ui/lib/theme-chalk/index.css';
import ElementUI from 'element-ui';
import Vant from 'vant';
import 'vant/lib/index.css';

Vue.use(Vant);

Vue.use(ElementUI)
Vue.config.productionTip = false


new Vue({
  // new实例化成功后会自动往这个实例挂一个$router这个属性
  router, // this.$router === router ,通过this.$router能访问到整个路由对象
  store, // this.$store === store ,通过this.$store能访问到整个store对象
  render: h => h(App) // vue 支持的新写法,相当于把App实例化后挂载到app节点上
}).$mount('#app')
