<template>
  <div v-if="filmInfo" class="box">
    <!-- <h1>详细页面</h1> -->
    <detail-header v-scroll="30">
      {{ filmInfo.name }}
    </detail-header>
    <div :style="{
      backgroundImage: 'url(' + filmInfo.poster + ')'
    }" class="poster"></div>
    <div class="content">
      <h4>{{ filmInfo.name }} {{ filmInfo.filmType.name }}</h4>
      <div class="son">{{ filmInfo.category }}</div>
      <div class="son">{{ filmInfo.premiereAt | dateFilter }} 上映 </div>
      <div class="son">{{ filmInfo.nation }} | {{ filmInfo.runtime }} 分钟</div>
      <div class="synopsis" :class="isHidden ? 'hidden' : ''">简介: {{ filmInfo.synopsis }}</div>
      <div class="icon" @click="isHidden = !isHidden"> <span class="iconfont icon-gengduo1"></span> </div>
    </div>
    <div class="actors">
      <h4>演职人员</h4>
      <detail-swiper :preview="3.5" name="actors">
        <detail-swiper-item v-for="item in filmInfo.actors" :key="item.name">
          <div :style="{
            backgroundImage: 'url(' + item.avatarAddress + ')'
          }" class="avatar"></div>
          <div class="son">{{ item.name }}</div>
          <div class="son">{{ item.role }}</div>
        </detail-swiper-item>
      </detail-swiper>
    </div>
    <!-- 这里出现轮播冲突 解决:父传子 实例不同的对象 -->
    <div class="figure">
      <h4>剧照 <span class="all">全部({{ filmInfo.photos.length }})</span></h4>
      <detail-swiper :preview="1.5" name="photos">
        <detail-swiper-item v-for="(item, index) in filmInfo.photos" :key="index">
          <div :style="{
            backgroundImage: 'url(' + item + ')'
          }" class="pianduan" @click="handlePreview(index)"></div>
        </detail-swiper-item>
      </detail-swiper>
    </div>
  </div>
</template>

<script>
import '../assets/iconfont-Al/iconfont.css'
import obj from '@/util/mixinObj'
import detailSwiper from '@/components/details/DetailSwiper.vue'
import detailSwiperItem from '@/components/details/DetailSwiperItem.vue'
import detailHeader from '@/components/details/DetailHeader.vue'
import { ImagePreview } from 'vant';
import Vue from 'vue';
import moment from 'moment'
// moment.locale('Zh-cn') //设置成中文
// console.log(moment().format('YYYY-MM-DD'));

Vue.directive('scroll', {
  inserted(el, binding) {
    el.style.display = 'none'
    window.onscroll = () => {
      if ((document.documentElement.scrollTop || document.body.scrollTop) >= binding.value) {
        el.style.display = 'block'
      } else {
        el.style.display = 'none'
      }
    }
  }
})
export default {
  mixins: [obj],
  data() {
    return {
      filmInfo: null,
      isHidden: true,

    }
  },

  created() {


    // this.$router拿到的是整个路由对象,包裹着所有路由
    // this.$route 拿到的是当前匹配的路由
    // console.log(this.$route);
    //通过里面params属性,可以获取传来的id
    // console.log(this.$route.params.myid);

    // 剩下的就是利用id发ajax请求,获取详细数据,布局页面
    // https://m.maizuo.com/gateway?filmId=6139&k=6661552 后面的k是随机的,防止重复拿回相同的数据
    fetch(`https://m.maizuo.com/gateway?filmId=${this.$route.params.myid}&k=6661552`, {
      headers: {
        'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.2.1","e":"16616703481265765516836865"}',
        'X-Host': 'mall.film-ticket.film.info'
      }
    }).then(res => res.json()).then(res => {
      // console.log(res.data.film);
      this.filmInfo = res.data.film
      // 加载消失(可利用axios拦截器)
      // Toast.clear()
    })
  },
  methods: {
    handlePreview(index) {

      // console.log(this.filmInfo.photos);
      // console.log(ImagePreview);
      ImagePreview({
        images: this.filmInfo.photos,
        startPosition: index,
        closeable: true,
      });
    }
  },
  filters: {
    dateFilter(date) {
      return moment(date * 1000).format('YYYY-MM-DD')
    }
  },
  // 自定义指令
  /* directives: {
    scroll(el, binding) {
      el.style.display = 'none'
      window.onscroll = () => {
        if ((document.documentElement.scrollTop || document.body.scrollTop) >= binding.value) {
          el.style.display = 'block'
        } else {
          el.style.display = 'none'
        }
      }
    }

  }, */
  destroyed() {
    window.onscroll = null
  },
  components: {
    detailSwiper,
    detailSwiperItem,
    detailHeader
  }
}
</script>

<style lang="scss" scoped>
.box {
  height: 1000px;
  background-color: rgb(214, 208, 208);
}

.poster {
  width: 100%;
  height: 219px;
  background-position: center;
  background-size: cover;
}

.content {
  padding: 10px;
  background: #fff;

  .son {
    font-size: 13px;
    color: rgb(79, 72, 72);
    margin-top: 5px;
  }

  .synopsis {

    text-overflow: ellipsis;

    line-height: 15px;
    font-size: 13px;
    color: #999;
    margin-top: 7px;
  }

  .hidden {
    overflow: hidden;
    height: 30px;
  }

  .icon {
    margin-top: 5px;
    text-align: center;
  }
}

.actors {
  padding: 10px;
  margin-top: 10px;
  background: #fff;

  h4 {
    margin-bottom: 10px;
  }

  .son {
    font-size: 13px;
    color: rgb(79, 72, 72);
    margin-top: 2px;
    text-align: center;
  }

  .avatar {
    width: 100%;
    height: 85px;
    background-position: center;
    background-size: cover;
  }
}

.figure {
  padding: 10px;
  margin-top: 10px;
  background: #fff;

  h4 {
    margin-bottom: 16px;

    .all {
      font-size: 12px;
      color: #666;
      float: right;
    }
  }

  .pianduan {
    width: 100%;
    height: 100px;
    background-position: center;
    background-size: cover;
  }
}
</style>