<template>
  <div>
    <h2 style="background: green;">nowplaying---二级路由</h2>

    <!-- vant实现懒加载 -->
    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad" :immediate-check="false">
      <van-cell v-for="item in list" :key="item.filmId" @click="handleChangePage(item.filmId)">
        <img :src="item.poster" alt="">
        <div class="right">
          <div class="title">{{ item.name }}</div>
          <div class="grade">观众评分: {{ item.grade ?? '无' }}</div>
          <div class="actor">主演: {{ item.actors | actorFilter }}</div>
          <div class="nation">{{ item.nation }} | {{ item.runtime }}分钟</div>
        </div>
        <!-- 声明式导航 -->
        <!-- <router-link to="/detail">{{item}}</router-link> -->
      </van-cell>
    </van-list>

  </div>
</template>

<script>
import http from '@/util/http'

export default {
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      refreshing: false,
      current: 1,
      total: 1 // 后端数据总长度
    }
  },
  created() {
    // 使用方法一封装的axios
    // http.httpForList(1).then(res => {
    //   this.list = res.data.data.films
    // })

    // 使用方法二封装的axios
    http({
      url: `/gateway?cityId=${this.$store.state.cityId}&pageNum=${this.current}&pageSize=7&type=1&k=5196770`,
      headers: {
        'X-Host': 'mall.film-ticket.film.list'
      }
    }).then(res => {
      // console.log(res.data.data);
      this.list = res.data.data.films
      this.total = res.data.data.total
    })

    // fetch(`https://m.maizuo.com/gateway?cityId=440100&pageNum=1&pageSize=10&type=1&k=5196770`, {
    //   headers: {
    //     'X-Client-Info': '{ "a": "3000", "ch": "1002", "v": "5.2.1", "e": "16616703481265765516836865" }',
    //     'X-Host': 'mall.film-ticket.film.list'
    //   }
    // }).then(res => {

    //   return res.json()
    // }).then(res => {
    //   // console.log(res.data.films);
    //   this.list = res.data.films
    // })

  },

  methods: {
    handleChangePage(id) {
      // 编程式导航
      // 方法一:
      // location.href = '#/detail'

      // vue提供的新写法 this.$router.push()
      // console.log(this.$router);
      // this.$router.push('/detail') // 要想知道哪个详情,需要传id

      // console.log(id);
      // 1.通过路径跳转
      // this.$router.push(`/detail/${id}`) //列表页负责发id,详情页负责接收id

      // 2.通过命名路由跳转
      this.$router.push({
        name: 'myownDetail',
        params: {
          myid: id // myid是前面定义路由规则时自己定义的
        }
      })
    },

    // 懒加载配置
    onLoad() {
      // console.log('到底了'); //一开始数据没回来就触发了
      if (this.list.length >= this.total) {
        return false
      }
      this.current++
      http({
        url: `/gateway?cityId=${this.$store.state.cityId}&pageNum=${this.current}&pageSize=7&type=1&k=5196770`,
        headers: {
          'X-Host': 'mall.film-ticket.film.list'
        }
      }).then(res => {
        this.list = [...this.list, ...res.data.data.films]
        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成,总长度匹配,禁用懒加载功能(从详情页返回的时候,因为数据还没回来造成相等,出现bug)
        if (this.list.length === this.total && this.total != 0) {
          this.finished = true;
        }
      })
    },
  },
  filters: {
    actorFilter(data) {
      if (data === undefined) {
        return '暂无主演'
      }
      return data.map(item => item.name).join(', ')
    }
  }
}

/* 
列表页到详情页的步骤
1.从后端取回列表页的数据布局
2.点完列表绑定事件进行动态路由跳转
3.到了详情页面中,前端获取id
4.利用id发请求给后端获取数据,最后进行布局
*/

/* 有些请求需要二次校验,即模拟header或cookie
 比如卖座电影的接口https://m.maizuo.com/gateway?cityId=440100&pageNum=1&pageSize=10&type=1&k=2976621  后面的k是随机数,防止重复拿回相同的数据
  可利用的小工具 postman
  添加X-client-Info
  和X-Host
*/
</script>

<style lang="scss" scoped>
.van-list {
  list-style: none;
  margin: 0;
}

.van-cell {
  overflow: hidden;
  padding: 5px;

  img {
    float: left;
    width: 66px;
  }

  .right {
    margin-left: 70px;

  }

  .title {
    font-size: 16px;
  }

  .grade,
  .nation,
  .actor {
    font-size: 12px;
  }

  .actor {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 200px;

  }
}
</style>