<template>
  <div>
    <button @click="handleClick">left</button>
    <h1>{{ myname }}</h1>
    <button v-show="myright">right</button>

    <slot></slot>
  </div>
</template>

<script>
export default {
  props: {
    myname: {
      type: String,
    },
    myright: {
      type: Boolean
    }
  },
  methods: {
    handleClick() {
      this.$emit('myevent')
    }
  },
}
</script>